import En from "./lang/en";
import Ru from  "./lang/ru";
import Uk from "./lang/uk";

const languages = {
  en: 'Eng',
  uk: 'Ð£ÐºÑ€',
  ru: 'Ð ÑƒÑ',
}

let titles = document.querySelectorAll('.selected_lang');
const showLists = document.querySelectorAll(".conteiner_langs");
const forms = document.querySelectorAll('.lang');
let isShowlist = false;
let lang = getLang();


document.addEventListener('DOMContentLoaded', () => {
  
  hiddeRadio(lang);
  chengeContent(lang);
  if(Object.keys(languages).includes(lang)) {
    titles.forEach(title => title.innerHTML = languages[lang]); 
      
  }else {
    titles.forEach(title => title.innerHTML = languages.en);
  }
});


// - here change lang
forms.forEach((form) => form.addEventListener('click', (e) => {
  if ([...e.target.classList].includes("langs_item")) {
    const currLang = e.target.children[0].className;
    hiddeRadio(currLang);
    setLang(currLang);
    chengeContent(currLang);
    titles.forEach(title => title.innerHTML = languages[currLang]);
  } 
}));



// - here hide box for custom option
document.addEventListener('click', (e) => {
  
  if(isShowlist) {
    showLists.forEach(showList => {
      showList.classList.remove("show");
      showList.classList.add("hidden");
    });
    isShowlist = false;
  }else if ([...e.target.classList].includes("selected_lang")) {
    showLists.forEach(showList => {
      showList.classList.remove("hidden");
      showList.classList.add("show");
    });
    isShowlist = true;
  } 
});

function chengeContent(lang) {
  let content = {};
  switch(lang) {
    case "uk":
      content = Uk;
      break;
      case "ru": 
      content = Ru;
      break;
      default:
        content = En;
  }

  for(let key in content) {
    let elements = document.querySelectorAll("." + key);
    elements.forEach(element => element.innerHTML = content[key]);
  }
}

function hiddeRadio(lang)  {
  let allRadios = document.querySelectorAll("input[type=radio]");
  allRadios.forEach(radio => {
    if([...radio.classList].includes(lang)) {
      radio.parentElement.style.display = "none";
      radio.checked = true;
    }else {
      radio.parentElement.style.display = "inherit";
      radio.cheked = false;
    }
  })
}

function getLang () {
  let lang_selected = window.localStorage.getItem("lang");
  if(lang_selected) {
    return JSON.parse(lang_selected);
  }
  lang_selected = window.navigator.userLanguage || window.navigator.language;
  lang_selected = lang_selected.slice(0, 2);
  setLang(lang_selected);
  window.localStorage.setItem("lang", JSON.stringify(lang_selected));
  return lang_selected;
}

function setLang (lang) {
  window.localStorage.setItem("lang", JSON.stringify(lang));
}

let isCheckBurger = false;
document.addEventListener('click', (e) => {
  if(e.target.tagName === 'A') return;
  let width = document.documentElement.clientWidth;
  const burgers = [...document.querySelectorAll(".burger_checkbox")];
  const checkBurger = burgers.find(burger => burger.checked === true);
  const selectedLang = document.querySelector(".selected_lang").className;
  if(width < 992) {
    if(e.target.className === selectedLang)return;
  }

  if(isCheckBurger){
    isCheckBurger = false;
    if(checkBurger){
  
      checkBurger.checked = false;

    };
  }else {
    if(checkBurger) {
      isCheckBurger = true;

    }
  }
});
 
const burgers = document.querySelectorAll(".burger_menue");
burgers.forEach(burger => {
  burger.addEventListener('click', (e) => {
    const chxbox = e.target.checked;
    if(chxbox) {
      burgers.forEach((burger) => {
        if(!burger.querySelector("input").checked) {
          burger.style.visibility = "hidden";
        }
      });
    }else {
        burgers.forEach(burger => {
        burger.style.visibility = "visible";
      })
    }
});
})








