

const listAnchorLinks = document.querySelectorAll("[href*='#']");
let animating = false;
const duration = 1200; 

listAnchorLinks.forEach(el => el.addEventListener('click', (e) => {
    e.preventDefault();
    const targetId = e.target.hash;
    const targetPositon = document.querySelector(targetId).offsetTop;
    smoothScrol(targetPositon + 5, duration);
    animating = true;
    setTimeout(() => animating = false , duration);

}));

function smoothScrol(targetPositon, duration) {
  const startPosition = window.pageYOffset;
  const distance = targetPositon - startPosition;
  let start = null;
  window.requestAnimationFrame(step);

  function step(timestamp) {
      if (!start) start = timestamp;
      const progress = timestamp - start;
      window.scrollTo(0, easeInOutCubic(progress, startPosition, distance, duration))
      if (progress < duration) window.requestAnimationFrame(step);
      
    }
}
 
function easeInOutCubic(t, b, c, d) {
	t /= d/2;
	if (t < 1) return c/2*t*t*t + b;
	t -= 2;
	return c/2*(t*t*t + 2) + b;
};

const home = document.querySelector("#home");
let currSection = "";
console.log(screen.width)

if(home && screen.width > 991) {

  
  document.querySelector('html').style.overflow = "hidden";
  const sections = [...document.querySelectorAll("section")];
  const reverseSections = [...sections].reverse(); 
  home.addEventListener('wheel', (e) => {

  let targetSection = sections.find(section => section.offsetTop > window.pageYOffset);
  if(animating)return;
    if(e.deltaY > 0) {
      targetSection = sections.find(section => section.offsetTop > window.pageYOffset);
      if(targetSection) smoothScrol(targetSection.offsetTop + 4, duration);
    }else {
      targetSection = reverseSections.find(section => (section.offsetTop + 5) < window.pageYOffset);
      if(targetSection)smoothScrol(targetSection.offsetTop + 4, duration);
    }
    animating = true;
    setTimeout(() => animating = false , duration);
    e.preventDefault();
  })
}

